import React from 'react';
import classNames from 'classnames';
import { Parse } from 'parse';
import ParseReact from 'parse-react';
const ParseComponent = ParseReact.Component(React);
import TitleBar from './TitleBar';
import BackButton from './BackButton';
import UserDropDown from './UserDropDown';
import NotificationCenter from './NotificationCenter';
import SpinnerButton from '../widgets/SpinnerButton';
import NotificationSystem from 'react-notification-system';
import UserTypeConstants from '../../constants/UserTypeConstants';

const contextTypes = {
  router: React.PropTypes.object.isRequired
};

class StoryNavigationBar extends ParseComponent {
  constructor(props) {
    super(props);

    this.state = {
      isDeleting: false,
      askUserAboutDeleting: false,
    };

    this.handleDelete = this.handleDelete.bind(this);
    this.handleExport = this.handleExport.bind(this);
  }

  componentDidMount() {
    this._notificationSystem = this.refs.notificationSystem;
  }

  observe() {
    return {
      stories: new Parse.Query('Story').equalTo('objectId', this.props.params.id),
      user: ParseReact.currentUser,
    };
  }

  handleDelete() {
    if (!this.state.askUserAboutDeleting) {
      this.setState({askUserAboutDeleting: true});
      this._notificationSystem.addNotification({
        message: 'Are you sure you want to delete this story? If so, click Delete Story again',
        level: 'error',
        position: 'tc'
      });
    } else {
      this.setState({isDeleting: true, askUserAboutDeleting: false});

      ParseReact.Mutation.Set(this.data.stories[0], {
        deleted: true
      }).dispatch().then(() => {
        this.context.router.push('/stories');
      }, (error) => {
        console.log('delete story error', error);
      });
    }
  }

  handleExport() {
    window.focus();
    window.print();
  }

  render() {
    const story = this.data.stories[0];
    let deleteButton;
    if (story && this.data.user && (this.data.user.objectId === story.creator.objectId || this.data.user.typeRaw === UserTypeConstants.TEACHER)) {
      deleteButton = (
        <div className="grid-block">
          <SpinnerButton
            className="button hollow"
            isSpinning={this.state.isDeleting}
            onClick={this.handleDelete}
          >Delete Story
          </SpinnerButton>
        </div>
      );
    }

    return (
      <TitleBar className="delete-story">
        <TitleBar.Left>
          <BackButton to="/stories"/>
        </TitleBar.Left>
        <TitleBar.Center>
          <div className="grid-block v-align align-center">
            {story && story.title}
          </div>
        </TitleBar.Center>
        <TitleBar.Right>
          <div className="grid-block">
            <div className="grid-block">
              <SpinnerButton
                className="button hollow"
                onClick={this.handleExport}
                >Export
              </SpinnerButton>
            </div>
            {deleteButton}
            <div className="grid-block">
              <NotificationCenter />
            </div>
            <div className="grid-block">
              <UserDropDown />
            </div>
          </div>
        </TitleBar.Right>
        <NotificationSystem ref="notificationSystem" />
      </TitleBar>
    );
  }
}

StoryNavigationBar.contextTypes = contextTypes;

export default StoryNavigationBar;



